.join-outer-container {
    display: flex;
    justify-content: center;
    text-align: center;
    height: 100vh;
    align-items: center;
    background-color: #1a1a1d;
}

.join-inner-container {
    width: 20%;
}

.join-input {
    border-radius: 0;
    padding: 15px 20px;
    width: 100%;
}

.heading {
    color: white;
    font-size: 2.5em;
    padding-bottom: 10px;
    border-bottom: 2px solid white;
}

.button {
    color: #fff !important;
    text-transform: uppercase;
    text-decoration: none;
    background: #2979ff;
    padding: 20px;
    border-radius: 5px;
    display: inline-block;
    border: none;
    width: 100%;
}

@media (min-width: 320px) and (max-width: 480px) {
    .join-outer-container {
        height: 100%;
    }

    .join-inner-container {
        width: 90%;
    }
}

button:focus {
    outline: 0;
}
